/* montserrat-regular - cyrillic_latin */
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/montserrat-v14-cyrillic_latin-regular.woff2") format("woff2"),
    url("../fonts/montserrat-v14-cyrillic_latin-regular.woff") format("woff");
}

